import React , { Component }from 'react';
import { CardList } from './components/card-list/card-list.component.jsx';
import {SearchBox} from './components/search-box/search-box.component';
import './App.css';

class App extends Component {
    constructor() {
        super();

        this.state = {
            char: [ ],
            searchField: ''
        };
    }

    componentDidMount() {
        fetch('https://api.jikan.moe/v3/anime/16498/characters_staff')
            .then(response => response.json())
            .then(users => this.setState({ char: users.characters }));
            //.then(users => console.log(users.characters))
    }

    handleChange = (seek) => {
        this.setState({ searchField: seek.target.value  })
    }

    render() {
        const { char, searchField } = this.state;

        const filteredChar= char.filter(char =>
            char.name
                .toLowerCase()
                .includes(searchField.toLowerCase())
        )
        return (
            <div className="App">
                <h1>Attack on Titan Characters</h1>
                <SearchBox
                    placeholder='search characters'
                    handleChange={this.handleChange}
                />
                <CardList characters={filteredChar} />

            </div>
        );
    }
}


export default App;
